import "bootstrap/dist/css/bootstrap.min.css"
import "../styles/app.css"
import React from "react"
import loadable from "@loadable/component"
// let x = 50
// let y = 50
export default () => {
  y = 0
  direction = "^"
  const setup = (p5, canvasParentRef) => {
    // use parent to render the canvas in this ref
    // (without that p5 will render the canvas outside of your component)
    p5.createCanvas(500, 500).parent(canvasParentRef)
  }

  const draw = (p5) => {
    p5.background(0)
    p5.fill(255, this.y * 1.3, 0)
    p5.ellipse(p5.width / 2, this.y, 50)
    if (this.y > p5.height) this.direction = ""
    if (this.y < 0) {
      this.direction = "^"
    }
    if (this.direction === "^") this.y += 8
    else this.y -= 4
  }
  if (typeof window !== "undefined") {
    const Sketch = loadable(() => import("react-p5"))
    return (
      <section>
        <div className="jumbotron m-4 bg-gradient-orange">
          <h1 className="display-4">Convoluciones con P5</h1>
          <p className="lead">lorem ipsum</p>
          <hr className="my-4"></hr>
          <p>lorem ipsum</p>
          <a className="btn btn-lg btn-dark" href="#" role="button">
            Convoluci√≥n
          </a>
        </div>
        <Sketch setup={setup} draw={draw} />
      </section>
    )
  } else {
    // if window does not exist
    return null
  }
}
